* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

input.box {
  font-family: 'Ubuntu', sans-serif;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
}

body {
  padding: 50px;
}

ol {
  margin: 0;
  padding: 0;
  list-style-position: inside;
}

.crossword {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 80%;
  margin: 0 10px;
  font-family: 'Ubuntu', sans-serif;
  font-size: 16px;
}

.clue {
  padding: 5px 0;
}

.clue-correct {
 text-decoration:line-through;
}

.clue-correct__text {
  padding-left: 15px;
  color: green;
  font-size: 1.1em;
}

.clue-lists {
    margin-right: 40px;
}

.hint-button {
  font-family: 'Ubuntu', sans-serif;
  font-size: 14px;
  margin-left: 10px;
  color: gray;
  display: block;
  margin-top: 10px;
}

.boxes {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: calc(50px * 7);
  min-width: 350px;
  min-height: 250px;
}

.box {
  width: 50px;
  height: 50px;
  border: 1px solid #000;
  text-align: center;
}

.box-wrapper {
  position: relative;
}

.box-label {
  position: absolute;
  top: 2px;
  left: 2px;
}

.box-answer.finished {
  border: 2px solid goldenrod;
  font-weight: bold;
  color: black;
  text-transform: uppercase;
  background-color: forestgreen;
  font-size: 1.2em;
}

.blank { 
  background: #333;
}

.solved {
  background: rgba(46,139,87, 0.8);
}

.link-image {
  margin-left: 10px;  
}

@media all and (max-width: 1030px) {
  .crossword {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  
  .clue-lists {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
}

@media only screen and (max-width: 600px) {
    body {
        padding: 5px;
    }

    .crossword {
      margin: 0 auto; 
      font-size: 14px;
    }

    .boxes {
      margin: 0 auto;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      width: calc(40px * 7);
      min-width: calc(40px * 7);
      min-height: calc(40px * 5);
    }

    .box {
      width: 40px;
      height: 40px;
    }

    input.box {
      font-size: 0.9em;
    }

    .link-container {
      text-align: center;
      margin: 20px auto;
    }

    .link-image {
      width: 50px;
      height: 50px;
      margin: 0 auto;
      -ms-flex-line-pack: center;
          align-content: center;
    }

    .link-text {
      font-weight: bold;
      font-size: 1.1em;
      text-align: center;
      margin: 20px auto;
    }
}



